@font-face {
  font-weight: normal;
  font-family: "Sample";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Sample.woff2") format("woff2"), url("../fonts/Sample.woff") format("woff"); }

@font-face {
  font-weight: bold;
  font-family: "Sample";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Sample.woff2") format("woff2"), url("../fonts/Sample.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  width: 100%;
  height: auto; }

input[type='search'] {
  -webkit-appearance: textfield; }

a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation; }

a svg,
button svg {
  pointer-events: none; }

svg:not(:root) {
  overflow: hidden; }

body {
  min-width: 320px;
  margin: 0 auto;
  font-weight: normal;
  font-size: 10px;
  line-height: 150%;
  font-family: "Sample", -apple-system, blinkmacsystemfont, "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", "Arial", sans-serif;
  color: #8e80a9;
  background-color: #fcf8fe;
  background-repeat: no-repeat;
  background-size: contain; }

